import React from "react";
import { useState, useEffect } from "react";

import { FaEllipsisV, FaWindowClose } from "react-icons/fa";
import styles from "../styles/Menu.module.css";
import Sidebar from "./sidebar";
const Menu = () => {
  const [showSidebar, setShowSidebar] = useState(false);
  useEffect(() => {
    const clickFunc = () => {
      setShowSidebar(false);
    };
    document.querySelector("#root").addEventListener("click", clickFunc);
    return () => {
      document.querySelector("#root").removeEventListener("click", clickFunc);
    };
  }, []);
  const handleClick = (e) => {
    setShowSidebar(!showSidebar);
    document.querySelector("#root").style.overflowY = showSidebar
      ? "scroll"
      : "hidden";
  };
  return (
    <>
      <section className={styles.menu} onClick={(e) => handleClick(e)}>
        {(!showSidebar && <FaEllipsisV />) || <FaWindowClose />}
      </section>
      <section
        className={`${styles.overlay} ${styles[showSidebar ? "active" : ""]}`}
      >
        {" "}
        <Sidebar />
      </section>
    </>
  );
};

export default Menu;
